.slider {
  display: flex;
  align-items: center;
  margin: 20px;
  opacity: 0;
  transform: scale(85%);
  transition: 0.3s;
  background-color: #efefef;
}

.slider_zoom {
  opacity: 1;
  transform: scale(100%);
  transition: 0.3s;
}

.slider_img {
  width: 60%;
}

.slider_content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.slider_title {
  text-align: center;
  padding: 10px 0px;
}

.slider_subtitle {
  text-align: justify;
  padding: 10px 0px;
}

@media screen and (max-width: 940px) and (min-width: 800px) {
  .slider_title {
    font-size: 1.6rem;
  }
}

@media screen and (max-width: 800px) {
  .slider {
    flex-direction: column;
  }
  .slider_img {
    width: 100%;
  }
  .slider_content:nth-child(odd) {
    order: 2;
  }
}
