.navbar {
  position: sticky;
  top: 0px;
  height: 70px;
  background-color: #000000;
  opacity: 0.75;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  margin-bottom: -70px;
  z-index: 5;
}

.navbar_logo,
.navbar_logo a {
  color: #ffffff;
  font-family: "MonteCarlo", cursive;
  font-size: 2.5rem;
  text-decoration: none;
}

.navbar_list {
  display: flex;
  list-style: none;
}

.navar_list_item {
  white-space: nowrap;
}

.navbar_link {
  font-size: 0.85rem;
  text-decoration: none;
  color: #ffffff;
  text-align: center;
  padding: 10px;
  margin: 0px 5px;
  border-style: solid;
  border-width: 0px 0px 1px 0px;
  border-color: transparent;
  transition: 250ms;
}

.navbar_link:hover {
  border-color: #ffffff;
  transition: 250ms;
}

.navbar_menu {
  color: #ffffff;
  cursor: pointer;
  display: none;
}

@media screen and (max-width: 800px) {
  .navbar_list {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    top: 70px;
    left: -100%;
    width: 100%;
    height: 300px;
    transition: all 0.4s;
  }

  .navbar_list-active {
    left: 0;
  }

  .navar_list_item {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0;
    flex: 1;
  }

  .navbar_link {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 0px;
    border-width: 0px;
    background-color: #000000;
    opacity: 0.9;
  }

  .navbar_link:hover {
    opacity: 1;
  }

  .navbar_menu {
    display: block;
  }
}
