.hero {
  position: relative;
  width: 100%;
  height: 100vh;
}

.hero_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

.hero_title {
  position: absolute;
  bottom: 15%;
  right: 10%;
  color: #ffffff;
  font-size: 4rem;
  padding: 15px;
}

@media screen and (max-width: 800px) {
  .hero_title {
    font-size: 2.5rem;
    padding: 0px 10px;
  }
}
